import React from "react";
import propTypes from "prop-types";
import "./Movie.scss";
import "./Img.scss";

function Img({ src, alt }) {
  return (
    <div className="Img__root">
      <img src={src} alt={alt} />
    </div>
  );
}

Img.propTypes = {
  alt: propTypes.string.isRequired,
  src: propTypes.string.isRequired,
};

export default Img;
